.jumbotron {
  background: url(/static/media/header-bg.01216605.jpg);
  background-size: cover;
  background-position: center;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.col-sm-12 {
  padding: 0;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 3rem;
  padding: 0;
  color: #333C40;
}

h2 {
  font-family: 'DM Serif Display', serif;
  font-weight: 700;
  font-style: italic;
  font-size: 2.5rem;
  color: #FFCB2C;
  text-shadow: 2px 1px #333C40;
}
.btn {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  color: white;
  background-color: #FFCB2C;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  align-self: center;
  line-height: 2rem;
}
.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

.card {
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: white;
  margin-top: 2rem;
}

.card-header {
  background-color: white;
}

.card-header h3 {
  font-family: 'DM Serif Display', serif;
  font-weight: 700;
  font-style: italic;
  font-size: 2rem;
  color: #FFCB2C;
  text-shadow: 2px 1px #333C40;
}


.container {
  display: flex; 
  flex-direction: column;
}

.card mb-3 {
  max-width: 540px;
  padding-left: 0;
}

.col-md-8 {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#availability-header {
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  text-align: center;
  padding: 2rem;
}

#resultsContainer{
  display: flex;
}
nav {
  background-color: #333C40;
  height: 5rem;
}

.navbar-brand {
  font-family: 'Kaushan Script', cursive;
  color: #FFCB2C;
  margin-left: 2rem;
}

.nav-links {
    font-family: 'Montserrat', sans-serif; 
    color: white;
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .nav-links li {
    margin: 1rem;
  }
  
  .nav-links a {
    border: 1px solid transparent;
    color: #292929;
    text-decoration: none;
    padding: 0.5rem;
  }
  
  .nav-links a:hover,
  .nav-links a:active,
  .nav-links a.active {
    color:  #FFCB2C;
  }
  
  .nav-links button {
    cursor: pointer;
    border: 1px solid #292929;
    color: #292929;
    background: transparent;
    padding: 0.5rem;
    font: inherit;
  }
  
  .nav-links button:focus {
    outline: none;
  }
  
  .nav-links button:hover,
  .nav-links button:active {
    background: #292929;
    color: white;
  }
  
  @media (min-width: 768px) {
    .nav-links {
      flex-direction: row;
    }
  
    .nav-links li {
      margin: 0 0.5rem;
    }
  
    .nav-links a {
      color: white;
      text-decoration: none;
    }
  
    .nav-links button {
      border: 1px solid white;
      color: white;
      background: transparent;
    }
    
    .nav-links button:hover,
    .nav-links button:active {
      background: #f8df00;
      color: #292929;
    }
  }
  

label {
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  align-self: center;
  justify-self: flex-start;
  font-weight: bold;
  margin: 0.5rem;
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.form-control {
  display: inline-flex;
  border: 0;
  justify-content: space-between;
}

input, textarea {
  font: inherit;
  border: 1px solid #ccc;
  background: #f8f8f8;
  padding: 0.15rem 0.25rem;
}

input:focus, textarea:focus {
  outline: none;
  background: #ebebeb;
  border-color: #FFCB2C;
}

.form-control--invalid label,
.form-control--invalid p {
  color: red;
}

.form-control--invalid input,
.form-control--invalid textarea {
  border: 1px solid red;
}


.button {
  font: inherit;
  padding: 0.5rem 1.5rem;
  border: 1px solid #FFCB2C;
  border-radius: 4px;
  background: #FFCB2C;
  color: white;
  cursor: pointer;
  margin-right: 1rem;
  text-decoration: none;
  display: inline-block;
}

.button:focus {
  outline: none;
}

.button:hover,
.button:active {
  background: #ff4382;
  border-color: #ff4382;
}

.button--inverse {
  background: #FFCB2C;
  color: #333C40;
  font-weight: 700;
}

.button--inverse:hover,
.button--inverse:active {
  color: white;
  background: #ff0055;
}

.button--danger {
  background: #830000;
  border-color: #830000;
}

.button--danger:hover,
.button--danger:active {
  background: #f34343;
  border-color: #f34343;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

.button--small {
  font-size: 0.8rem;
}

.button--big {
  font-size: 1.5rem;
}
.authentication__header {
  color: white;
  text-align: center;
}

.authentication {
  width: 90%;
  max-width: 25rem;
  margin: 7rem auto;
  text-align: center;
}

.authentication form {
  margin-bottom: 1rem;
}
